



























import Vue from "vue";

import ChallengeCard from "@/components/ChallengeCard.vue";

import challenges from "../data/challenges.json";

// class Challenge {
//   id!: number
//   title!: string
//   description!: string
//   difficulty!: number
//   completed!: boolean
//   lastUpdated!: number
//   score!: number
// }

export default Vue.extend({
  name: "Challenges",
  components: {
    ChallengeCard
  },
  data() {
    return {
      challenges: challenges
    };
  },
  computed: {
    incomplete() {
      return challenges.filter(it => !it.completed);
    },
    completed() {
      return challenges.filter(it => it.completed);
    }
  }
});
